
/* ----------------------------------------------------------------------
   Page loader
-------------------------------------------------------------------------*/


body:not(.no-page-loader) #wrapper,
.animsition-overlay {
    opacity: 0; 
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.animsition-overlay,
.animsition-overlay-slide {
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: #fff;
}
 /* loading option */
.animsition-loading,
.animsition-loading:after {
  width: 32px;
  height: 32px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -16px;
  margin-left: -16px;
  border-radius: 50%;
  z-index: 100;
}

.animsition-loading {
      background-color: $color-white;
    width: 100%;
    height: 100%;

}



