}

.pacman>div:first-of-type,
.pacman>div:nth-child(2) {
    border-color: $color-theme transparent $color-theme $color-theme;
}




body[data-icon]::before {
	display: none;
	font-family: none;
}


.loader01::after,
.loader04::after,
