/* ----------------------------------------------------------------------
	Headings
-------------------------------------------------------------------------*/

// DELETE VARIABLES
// Heading fancy
.heading-fancy {
    position: relative;
    margin-bottom: 30px;
    clear: both;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        background-color: #FFF;
        padding-right: 15px;
        margin-bottom: 0;
        position: relative;
        display: inline-block;
    }
    h4,
    h5,
    h6 {
        padding-right: 10px;
    }
    // Heading Line
    &.heading-line:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 0;
        border-top: 1px solid #E5E5E5;
        left: auto;
        top: 49%;
        right: 0;
    }
    &.text-center {
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            padding-left: 15px;
        }
    }
    &.text-right {
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            padding-left: 15px;
            padding-right: 0;
        }
    }
}
// Heading jumbo
.heading-jumbo {
    font-size: 70px;
    font-weight: 600;
    line-height: 1;
    margin: 0 0 0.2em;
}
.heading-title-border-bottom {
    border-bottom: 1px solid;
    border-bottom-color: #e7e7e7;
}
.heading-title-simple {
    margin-bottom: 30px;
    display: block;
    padding: 0 0 10px;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin-bottom: 0;
        position: relative;
    }
}
// HR title
.hr-title {
    border-top-style: solid;
    border-top-width: 1px;
    font-size: 16px;
    text-align: center;
    height: 10px;
    line-height: 20px;
    margin: 10px 0;
    height: auto;
    color: #bbbbbb;
    abbr {
        background-color: #ffffff;
    }
    abbr {
        padding: 2px 10px;
        border-radius: 2px;
        position: relative;
        top: -10px;
        letter-spacing: .2em;
        text-transform: uppercase;
    }
    i {
        position: relative;
        top: -2px;
        font-size: 8px;
    }
    &.hr-full {
        width: 100%;
    }
    &.hr-long {
        width: 50%;
        margin: 20px auto 30px;
    }
    &.hr-short {
        width: 25%;
        margin: 20px auto 30px;
    }
    &.hr-left {
        text-align: left;
        abbr {
            padding-left: 0;
        }
        &.hr-long {
            margin-left: 0;
        }
    }
    &.hr-right {
        text-align: right;
        abbr {
            padding-right: 0;
        }
        &.hr-long {
            margin-right: 0;
        }
    }
    &.hr-double {
        border-top: 4px double #ebebeb;
        abbr {
            top: -12px;
        }
    }
}
// Heading
.heading {
    margin-bottom: 100px;
    text-align: center;
    > h1,
    > h2 {
        font-size: 40px;
        font-family: $font-family-5;
        text-transform: capitalize;
        line-height: 46px;
        font-weight: 300;
    }
    > h1,
    > h2,
    > h4,
    > p {
        margin-bottom: default-bottom-margin;
    }
    &.section-title {
        > h1,
        > h2 {
            font-size: 48px;
            line-height: 56px;
        }
        > h1,
        > h2,
        > h4,
        > p {
            margin-bottom: 20px;
        }
        p {
            font-size: 20px;
            font-weight: 300;
            line-height: 30px;
            span {
                font-weight: 600;
            }
        }
    }
}

