// heading light
.heading.heading-light,
.text-light .heading,
.heading.heading-light p,
.heading.heading-light .lead,
.heading.heading-light h2,
.heading.heading-light h3 {
    color: #fff !important;
}
.heading.heading-light:after,
.text-light .heading:after {
    border-top: 4px solid #fff;
}
// hero headings
.heading-hero {
    border: 2px solid #ffffff;
    color: #ffffff;
    font-family: Raleway;
    font-size: 50px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: 0;
    line-height: 60px;
    margin: 0;
    padding: 0;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    padding: 8px 21px !important;
    position: relative;
    margin-bottom: 30px;
}
.text-center a .title,
.text-right a .title {
    width: 100%;
    left: 0;
}
.text-right {
    a .title {
        left: auto;
        right: 30px;
        width: 100%;
    }
}
//Fancy headings
.fancy-title {
    position: relative;
    margin-bottom: 30px;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        position: relative;
        display: inline-block;
        background-color: #FFF;
        padding-right: 15px;
        margin-bottom: 0;
    }
    h4,
    h5,
    h6 {
        padding-right: 10px;
    }
    &.title-double-border,
    &.title-border,
    &.title-border-color {
        &:before {
            content: '';
            position: absolute;
            width: 100%;
            height: 0;
            border-top: 3px double #E5E5E5;
            left: auto;
            top: 46%;
            right: 0;
        }
    }
    &.title-border {
        &:before {
            top: 49%;
            border-top: 1px solid #EEE;
        }
    }
    &.title-border-color {
        &:before {
            top: 49%;
            border-top: 1px solid $color-theme;
            opacity: 0.6;
        }
    }
}

/* Fancy Title - Center Align
-----------------------------------------------------------------*/

.title-center {
    text-align: center;
    h1,
    h2,
    h3 {
        padding: 0 15px;
    }
    h4,
    h5,
    h6 {
        padding: 0 10px;
    }
}

/* Fancy Title - Right Align
-----------------------------------------------------------------*/

.title-right {
    text-align: right;
    h1,
    h2,
    h3 {
        padding: 0 0 0 15px;
    }
    h4,
    h5,
    h6 {
        padding: 0 0 0 10px;
    }
}

/*Creative fade heading*/

.heading-creative {
    font-family: $font-family-3;
    font-size: 62px;
    font-weight: 100;
    position: relative;
    text-align: left;
    width: 100%;
    strong {
        color: $color-theme;
    }
}
