/* ----------------------------------------------------------------
    Header
-----------------------------------------------------------------*/

#header {
    position: relative;
    width: 100%;
    z-index: 199 !important;
    height: $header-height;
    line-height: $header-height;

    .container {
        position: relative;
    }

    #header-wrap {
        height: $header-height;
        background-color: $color-white;
        -webkit-backface-visibility: hidden;
        left: 0;
        right: 0;
        transition: all .4s ease-in-out;
        border-bottom: 1px solid $light;

        /*logo*/
        #logo {
            float: left;
            font-size: 28px;
            position: relative;
            z-index: 1;
            transition: all .4s ease-in-out;
            height: $header-height;

            a {
                > img {
                    vertical-align: inherit;
                    transition: all .4s ease-in-out;
                    height: $header-height;
                    width: auto;
                }
            }
        }
    }

    /*sticky version*/
    &.header-sticky {
        #header-wrap {
            background-color: $color-white !important;
            transition: top .8s ease-in-out;
            position: fixed;
            top: -($header-height + 60);
            box-shadow: 0 12px 6px rgba(0, 0, 0, 0.06);

            #logo {
                font-size: 20px;
            }
        }

        &.sticky-active #header-wrap {
            top: 0;
        }

        &.dark {
            #header-wrap {
                background-color: $dark !important;
            }
        }
    }

    /*dark*/
    &.dark {
        #header-wrap {
            background-color: $dark;

            .lines,
            .lines:before,
            .lines:after {
                background-color: $color-white;
            }

            border-bottom: 1px solid $darker;
        }

        /*Main Menu*/
        #mainMenu {
            nav {
                > ul {
                    > li {
                        &:before {
                            color: $dark-light;
                        }

                        > a {
                            color: $color-white;

                            .menu-sub-title {
                                color: $light;
                            }
                        }

                        &.current,
                        &:hover,
                        &:focus {
                            > a {
                                &:after {
                                    background-color: $color-white;
                                }
                            }
                        }
                    }
                }
            }
        }

        // #top-search-trigger {
        .header-extras {
            i {
                color: $color-white;
            }

            .lines,
            .lines:before,
            .lines:after {
                background-color: $color-white;
            }
        }
    }

    /*transparent*/
    &.header-transparent,
    &.header-transparent.dark {
        #header-wrap {
            background-color: transparent;
            border-bottom: 0;
        }
    }

    /*transparent dark*/
    &.header-dark-transparent {
        #header-wrap {
            background-color: rgba($dark, 0.4);
            border-bottom: 0;
        }
    }

    /*transparent light*/
    &.header-light-transparent,
    &.header-light-transparent.dark {
        #header-wrap {
            background-color: rgba($color-white, 0.8);
            border-bottom: 0;
        }
    }

    /*transparent light*/
    &.header-colored-transparent,
    &.header-colored-transparent.dark {
        #header-wrap {
            background-color: rgba($color-theme, 0.8);
            border-bottom: 0;
        }

        &.header-sticky {
            #header-wrap {
                background-color: $color-white !important;
            }
        }
    }

    &.header-transparent,
    &.header-light-transparent,
    &.header-dark-transparent,
    &.header-colored-transparent {

        + #slider,
        + #page-title:not(.page-title-classic),
        + .fullscreen,
        + .halfscreen,
        + #google-map {
            top: -$header-height;
            margin-bottom: -$header-height;
        }

        + #page-title:not(.page-title-classic) {
            padding-top: 140 + $header-height;
        }
    }

    &.header-fullwidth {
        #header-wrap {
            .container {
                max-width: 100%;
                padding: 0 30px;
            }

            .header-extras {
                border-left: 1px solid $light;
                padding-left: 4px;
                margin-left: 14px;
            }
        }

        &.header-transparent {
            #header-wrap {
                .header-extras {
                    border-left: 1px solid rgba(144, 144, 144, 0.25);
                }

                border-bottom: 1px solid rgba(144, 144, 144, 0.25);
            }

            &.dark {
                #header-wrap {
                    .header-extras {
                        border-left: 1px solid rgba(231, 231, 231, .25);
                    }

                    border-bottom: 1px solid rgba(231, 231, 231, .25);
                }
            }
        }
    }

    &.header-modern:not(.header-active) {
        margin: 40px auto 0;
        width: 1200px;

        + #slider,
        + .fullscreen,
        + .halfscreen,
        + #google-map {
            top: -(50 + $header-height);
            margin-bottom: -(50 + $header-height);
        }

        #header-wrap {
            background-color: $color-white;
            border-radius: 4px;
        }

        &.dark {
            #header-wrap {
                background-color: $dark;
            }
        }

        &.header-transparent {
            #header-wrap {
                background-color: transparent;
            }
        }

        &.header-colored {
            #header-wrap {
                background-color: $color-theme;
                border-bottom: 0;
            }
        }
    }

    /*colored*/
    &.header-colored {
        #header-wrap {
            background-color: $color-theme;
            border-bottom: 0;
        }
    }

    &.header-mini {
        line-height: - 20 + $header-height;
        height: -20 + $header-height;

        #header-wrap {
            height: -20 + $header-height;

            #mainMenu {
                > .container {
                    nav > ul > li {
                        > a {
                            font-size: 11px;
                        }

                        .dropdown-submenu:before {
                            top: -6px;
                        }
                    }
                }
            }

            #logo {
                height: -20 + $header-height;

                a {
                    > img {
                        /*  width: 100%; */
                        height: -20 + $header-height;
                    }
                }
            }

            #mainMenu-trigger {
                height: -20 + $header-height;
            }
        }
    }

    &.header-logo-right {
        #header-wrap {
            #logo {
                float: right;
            }

            .header-extras {
                float: left;

                ul > li:first-child {
                    margin-left: 0px;
                }
            }

            #mainMenu nav {
                float: left;
            }
        }
    }

    &.header-static {
        #header-wrap {
            position: fixed !important;
            top: 0 !important;
            background-color: $color-white;
        }
    }

    &.header-plain {
        #header-wrap {
            background-color: transparent;
            border: 0 !important;

            .header-extras {
                border: 0 !important;
            }
        }
    }

    &.header-shadow {
        #header-wrap {
            box-shadow: 0 0 18px 0 rgba(0, 0, 0, .12) !important;
        }
    }

    /*header extras*/
    .header-extras {
        float: right;
        z-index: 201;
        position: relative;

        > ul {
            list-style: none;
            padding: 0;
            margin: 0;

            > li {
                float: left;
                margin-left: 20px;
                border: 0;

                > a:not(.btn),
                .topbar-dropdown > .title {
                    font-family: $menu-font;
                    font-size: 12px;
                    position: relative;
                    display: block;
                    font-style: normal;
                    text-transform: uppercase;
                    font-weight: 700;

                    i {
                        font-size: 14px;
                        position: relative;

                        &:nth-of-type(2) {
                            visibility: hidden;
                            position: absolute;
                        }
                    }

                    &.toggle-active {
                        i {
                            &:nth-of-type(2) {
                                visibility: visible;
                                position: relative;
                            }

                            &:nth-of-type(1) {
                                visibility: hidden;
                                position: absolute;
                            }
                        }
                    }
                }

                .btn {
                    font-size: 13px;
                    line-height: 1.3;
                    margin-bottom: 0px;
                }
            }
        }

        /* Shoping cart*/
        #shopping-cart {
            position: relative;

            > a {
                position: relative;

                .shopping-cart-items {
                    position: absolute;
                    z-index: 1;
                    background: $color-theme;
                    border-radius: 50%;
                    color: $color-white;
                    font-size: 9px;
                    font-weight: 600;
                    height: 13px;
                    line-height: 10px;
                    padding: 2px 4px;
                    right: -6px;
                    top: -6px;
                    width: 13px;
                }
            }
        }

        .topbar-dropdown {
            .title {
                line-height: inherit !important;
                letter-spacing: 1px;
                padding: 0;
                color: $dark;
            }

            .dropdown-list {
                right: -10px;
                margin-top: -20px;
                left: auto;
                box-shadow: 0px -6px 16px rgba(0, 0, 0, 0.1);

                &:before {
                    content: " ";
                    width: 0;
                    height: 0;
                    border-left: 9px solid transparent;
                    border-right: 9px solid transparent;
                    border-bottom: 9px solid $color-white;
                    top: -9px;
                    right: 14px;
                    position: absolute;
                }

                margin-top: -16px;
            }
        }
    }

    /*Responsive mobile menu*/
    #mainMenu-trigger {
        position: absolute;
        opacity: 0;
        visibility: hidden;
        height: $header-height;
        z-index: 1;

        button {
            background: none;
            border: 0;
            padding: 0;

            &:focus {
                outline: 0;
            }

            i {
                font-size: 17px;
            }
        }
    }
}


/* ----------------------------------------------------------------
Main Menu
-----------------------------------------------------------------*/

#mainMenu {
    padding: 0;

    > .container {
        padding: 0 !important;
        /*   width: 100%;
        max-width: 1170px;*/
    }

    [class*="col-lg-"] {
        line-height: 24px !important;
    }

    nav {
        float: right;

        > ul {
            list-style: none;
            padding: 0;
            margin: 0;

            > li {
                float: left;
                border: 0;
                transition: all .3s ease;
                margin-left: 6px;

                > a {
                    position: relative;
                    font-family: $menu-font;
                    padding: 11px 14px;
                    font-size: 12px;
                    font-weight: 700;
                    letter-spacing: 1px;
                    color: $dark;
                    text-transform: uppercase;
                    border-radius: 0;
                    border-width: 0;
                    border-style: solid;
                    border-color: transparent;
                    transition: all .3s ease;
                    line-height: normal;

                    > i {
                        margin-left: 2px;
                        margin-right: 6px;
                        min-width: 14px;
                        font-size: 14px;
                    }

                    &:after,
                    &:before {
                        transition: all .3s ease;
                    }
                }

                &.hover-active,
                &.current,
                &:hover,
                &:focus {

                    > a,
                    > span {
                        color: $color-theme;
                    }
                }

                .dropdown-menu {
                    background-position: right bottom;
                    background-repeat: no-repeat;
                    min-width: 230px;
                    top: auto;
                    background-color: $color-white;
                    border: 0;
                    border-style: solid;
                    border-color: $light;
                    border-width: 1px !important;
                    left: auto;
                    margin: 0;
                    margin-top: -6px;
                    border-radius: 4px;
                    box-shadow: 0px 0px 18px 0 rgba(0, 0, 0, 0.05);
                    padding: 10px;

                    > li {

                        > a,
                        > span,
                        [class*="col-"] > ul > li > a {
                            font-family: $font-family-2;
                            font-size: 13px;
                            line-height: 14px;
                            font-weight: 400;
                            font-style: normal;
                            color: $slate-grey;
                            padding: 12px 20px 12px 18px;
                            display: block;
                            transition: .3s ease;

                            &:focus,
                            &:hover,
                            &:active {
                                background-color: transparent;
                                color: $color-theme;
                                cursor: pointer;
                            }

                            > i {
                                margin-left: -4px;
                                margin-right: 6px;
                                min-width: 14px;
                            }
                        }

                        &.current,
                        &:hover,
                        &:focus,
                        &:active,
                        &.hover-active {

                            > a,
                            > span {
                                color: $color-theme;
                            }
                        }
                    }
					
					 &.menu-last {
                            right: 0;
                            left: auto;
                        }
                }

                &.mega-menu-item {
                    position: static;

                    > .dropdown-menu {
                        left: 0;
                        right: 0;
                        overflow: hidden;
                        width: 1140px;
                        max-width: 100%;
                        transition-duration: 0.3s !important;
                        z-index: 1;
                    }

                    .mega-menu-content {
                        padding: 20px;

                        .mega-menu-title {
                            font-family: $font-family-2;
                            font-size: 13px;
                            line-height: 14px;
                            font-weight: 700;
                            text-transform: uppercase;
                            padding-bottom: 12px;
                        }

                        li ~ .mega-menu-title {
                            margin-top: 12px;
                        }

                        > a,
                        > span,
                        [class*="col-"] > ul > li > a {
                            padding: 10px 0px 10px 0px;

                            > i {
                                margin-left: 0px;
                                margin-right: 8px;
                            }
                        }

                        [class*="col-"] > ul > li:first-child > a {
                            padding-top: 0;
                        }

                        [class*="col-"] > ul > li:last-child > a {
                            padding-bottom: 0;
                        }
                    }
                }

                .dropdown-submenu {
                    position: relative;

                    &:before {
                        line-height: 40px;
                        content: "\e95a";
                        font-family: "inspiro-icons";
                        opacity: 0.6;
                        position: absolute;
                        right: 16px;
                        color: $dark-grey;
                        font-size: 12px;
                        transition: all .2s ease;
                    }

                    > .dropdown-menu {
                        margin-left: -6px;
                        left: 100%;
                        top: -1px;

                        &.menu-invert {
                            right: 100%;
                            left: auto;
                        }
						
						
                    }
					

                    &:not(.hover-active):hover {
                        &:before {
                            right: 12px;
                        }

                        > ul {
                            display: block;
                            animation: fade-in-out .4s ease;
                        }
                    }
                }

                &:not(.hover-active):hover > ul {
                    display: block;
                    animation: fade-in-out .4s ease;
                }

                &:last-child {
                    margin-right: 0;
                }
            }

            > a.btn {
                margin-top: 4px;
            }

            li ul {
                list-style: none;
                padding: 0;
            }

            .badge {
                font-size: 8px;
                padding: 3px 4px;
                line-height: 8px;
                margin: 0 4px;
            }
        }
    }

    &.menu-fullwidth {
        nav {
            > ul {
                > li {
                    &.mega-menu-item {
                        > .dropdown-menu {
                            left: 0 !important;
                            right: 0 !important;
                            width: 100%;
                        }
                    }
                }
            }
        }
    }

    /*Menu Styles*/
    &.menu-rounded {
        nav {
            > ul {
                > li {
                    > a {
                        border-radius: 4px;
                        border-width: 2px;
                    }

                    &.current,
                    &:hover,
                    &:focus {
                        > a {
                            background-color: $color-theme;
                            color: $color-white;
                        }
                    }
                }
            }
        }
    }

    &.menu-outline {
        nav {
            > ul {
                > li {
                    > a {
                        border-radius: 50px;
                        border-width: 2px;
                    }

                    &.current,
                    &:hover,
                    &:focus {
                        > a {
                            color: $color-theme;
                            border-color: $color-theme;
                            background-color: transparent;
                        }
                    }
                }
            }
        }
    }

    &.menu-sub-title {
        nav {
            > ul {
                > li {
                    line-height: 64px;

                    > a {
                        > .menu-sub-title {
                            font-size: 10px;
                            padding: 0px;
                            margin-top: -14px !important;
                            line-height: 0;
                            height: 0;
                            text-transform: none;
                            display: block;
                            opacity: 0.5;
                            font-weight: 400;
                            margin: 0;
                            margin-top: 0px;
                            margin-bottom: 0px;
                            margin-bottom: -50px;
                            margin-left: 14px;
                            margin-right: 14px;
                        }
                    }

                    .dropdown-submenu {
                        &:before {
                            top: -20px;
                        }
                    }
                }
            }
        }
    }

    &.menu-lines {
        nav {
            > ul {
                > li {
                    margin-left: 4px;

                    > a {
                        border-radius: 0;
                        border: none;

                        &:after {
                            content: '';
                            position: absolute;
                            top: auto;
                            bottom: -2px;
                            left: 50%;
                            width: 0;
                            height: 2px;
                        }
                    }

                    &.current,
                    &:hover,
                    &:focus {
                        > a {
                            color: $color-theme;

                            &:after {
                                width: 100%;
                                left: 0%;
                                background-color: $color-theme;
                            }
                        }
                    }
                }
            }
        }
    }

    &.menu-creative {
        nav {
            > ul {
                > li {
                    margin-left: 4px;

                    > a {
                        &:after {
                            background-color: $color-white;
                            content: "";
                            height: 2px;
                            left: 0;
                            opacity: 0;
                            position: absolute;
                            top: 100%;
                            transform: translateY(10px);
                            transition: opacity 0.3s ease 0s, transform 0.3s ease 0s;
                            width: 100%;
                            bottom: -2px;
                        }
                    }

                    &.current,
                    &:hover,
                    &:focus {
                        > a {
                            &:after {
                                opacity: 1;
                                transform: translateY(0px);
                            }
                        }
                    }
                }
            }
        }
    }

    &.menu-slide {
        nav {
            > ul {
                > li {
                    visibility: hidden;
                    filter: alpha(opacity=0);
                    -webkit-opacity: 0;
                    -moz-opacity: 0;
                    opacity: 0;
                    transition: all 0s cubic-bezier(.175, .885, .525, 1.2);
                    -webkit-transition-duration: 150ms;
                    -moz-transition-duration: 150ms;
                    -o-transition-duration: 150ms;
                    transition-duration: 150ms;
                }

                &:nth-child(n+1) {
                    -webkit-transition-duration: 250ms;
                    -moz-transition-duration: 250ms;
                    -o-transition-duration: 250ms;
                    transition-duration: 250ms;
                    -webkit-transition-delay: 60ms;
                    -moz-transition-delay: 60ms;
                    -o-transition-delay: 60ms;
                    transition-delay: 60ms;
                }

                &:nth-child(n+2) {
                    -webkit-transition-duration: 350ms;
                    -moz-transition-duration: 350ms;
                    -o-transition-duration: 350ms;
                    transition-duration: 350ms;
                    -webkit-transition-delay: 120ms;
                    -moz-transition-delay: 120ms;
                    -o-transition-delay: 120ms;
                    transition-delay: 120ms;
                }

                &:nth-child(n+3) {
                    -webkit-transition-duration: 450ms;
                    -moz-transition-duration: 450ms;
                    -o-transition-duration: 450ms;
                    transition-duration: 450ms;
                    -webkit-transition-delay: 180ms;
                    -moz-transition-delay: 180ms;
                    -o-transition-delay: 180ms;
                    transition-delay: 180ms;
                }

                &:nth-child(n+4) {
                    -webkit-transition-duration: 550ms;
                    -moz-transition-duration: 550ms;
                    -o-transition-duration: 550ms;
                    transition-duration: 550ms;
                    -webkit-transition-delay: 240ms;
                    -moz-transition-delay: 240ms;
                    -o-transition-delay: 240ms;
                    transition-delay: 240ms;
                }

                &:nth-child(n+5) {
                    -webkit-transition-duration: 650ms;
                    -moz-transition-duration: 650ms;
                    -o-transition-duration: 650ms;
                    transition-duration: 650ms;
                    -webkit-transition-delay: .3s;
                    -moz-transition-delay: .3s;
                    -o-transition-delay: .3s;
                    transition-delay: .3s;
                }

                &:nth-child(n+6) {
                    -webkit-transition-duration: 750ms;
                    -moz-transition-duration: 750ms;
                    -o-transition-duration: 750ms;
                    transition-duration: 750ms;
                    -webkit-transition-delay: 360ms;
                    -moz-transition-delay: 360ms;
                    -o-transition-delay: 360ms;
                    transition-delay: 360ms;
                }

                &:nth-child(n+7) {
                    -webkit-transition-duration: 750ms;
                    -moz-transition-duration: 750ms;
                    -o-transition-duration: 750ms;
                    transition-duration: 750ms;
                    -webkit-transition-delay: 420ms;
                    -moz-transition-delay: 420ms;
                    -o-transition-delay: 420ms;
                    transition-delay: 420ms;
                }

                &:nth-child(n+8) {
                    -webkit-transition-duration: 750ms;
                    -moz-transition-duration: 750ms;
                    -o-transition-duration: 750ms;
                    transition-duration: 750ms;
                    -webkit-transition-delay: 480ms;
                    -moz-transition-delay: 480ms;
                    -o-transition-delay: 480ms;
                    transition-delay: 480ms;
                }
            }
        }

        &.items-visible {
            nav {
                > ul {
                    > li {
                        visibility: visible;
                        filter: alpha(opacity=100);
                        -webkit-opacity: 1;
                        -moz-opacity: 1;
                        opacity: 1;

                        &:nth-child(n+1) {
                            -webkit-transition-duration: 250ms;
                            -moz-transition-duration: 250ms;
                            -o-transition-duration: 250ms;
                            transition-duration: 250ms;
                            -webkit-transition-delay: 60ms;
                            -moz-transition-delay: 60ms;
                            -o-transition-delay: 60ms;
                            transition-delay: 60ms;
                        }

                        &:nth-child(n+2) {
                            -webkit-transition-duration: 350ms;
                            -moz-transition-duration: 350ms;
                            -o-transition-duration: 350ms;
                            transition-duration: 350ms;
                            -webkit-transition-delay: 120ms;
                            -moz-transition-delay: 120ms;
                            -o-transition-delay: 120ms;
                            transition-delay: 120ms;
                        }

                        &:nth-child(n+3) {
                            -webkit-transition-duration: 450ms;
                            -moz-transition-duration: 450ms;
                            -o-transition-duration: 450ms;
                            transition-duration: 450ms;
                            -webkit-transition-delay: 180ms;
                            -moz-transition-delay: 180ms;
                            -o-transition-delay: 180ms;
                            transition-delay: 180ms;
                        }

                        &:nth-child(n+4) {
                            -webkit-transition-duration: 550ms;
                            -moz-transition-duration: 550ms;
                            -o-transition-duration: 550ms;
                            transition-duration: 550ms;
                            -webkit-transition-delay: 240ms;
                            -moz-transition-delay: 240ms;
                            -o-transition-delay: 240ms;
                            transition-delay: 240ms;
                        }

                        &:nth-child(n+5) {
                            -webkit-transition-duration: 650ms;
                            -moz-transition-duration: 650ms;
                            -o-transition-duration: 650ms;
                            transition-duration: 650ms;
                            -webkit-transition-delay: .3s;
                            -moz-transition-delay: .3s;
                            -o-transition-delay: .3s;
                            transition-delay: .3s;
                        }

                        &:nth-child(n+6) {
                            -webkit-transition-duration: 750ms;
                            -moz-transition-duration: 750ms;
                            -o-transition-duration: 750ms;
                            transition-duration: 750ms;
                            -webkit-transition-delay: 360ms;
                            -moz-transition-delay: 360ms;
                            -o-transition-delay: 360ms;
                            transition-delay: 360ms;
                        }

                        &:nth-child(n+7) {
                            -webkit-transition-duration: 750ms;
                            -moz-transition-duration: 750ms;
                            -o-transition-duration: 750ms;
                            transition-duration: 750ms;
                            -webkit-transition-delay: 420ms;
                            -moz-transition-delay: 420ms;
                            -o-transition-delay: 420ms;
                            transition-delay: 420ms;
                        }

                        &:nth-child(n+8) {
                            -webkit-transition-duration: 750ms;
                            -moz-transition-duration: 750ms;
                            -o-transition-duration: 750ms;
                            transition-duration: 750ms;
                            -webkit-transition-delay: 480ms;
                            -moz-transition-delay: 480ms;
                            -o-transition-delay: 480ms;
                            transition-delay: 480ms;
                        }
                    }
                }
            }
        }
    }

    &.menu-hover-background {
        nav {
            > ul {
                > li {
                    margin-left: 0;

                    > a {
                        background: none;
                    }

                    &.current,
                    &:hover,
                    &:focus {
                        background-color: $color-theme;

                        > a {
                            color: $color-white !important;
                        }
                    }
                }

                .dropdown-menu {
                    margin-top: 0;
                    border-radius: 0;
                }
            }
        }
    }

    &.menu-overlay {
        nav {
            opacity: 0 !important;
            pointer-events: none;
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            height: 100% !important;
            background: rgba($dark, 0.99);
            transition: opacity 0.5s ease;

            > ul {
                transition: transform 1s ease;
                transform: translateY(-80%);
                top: -80%;
                position: relative;

                > li {
                    float: none;
                    text-align: center;
                    max-width: 400px;
                    margin: 0 auto;
                    padding: 0px;
                    line-height: 40px;

                    > a {
                        opacity: 0;
                        font-size: 32px;
                        padding-top: 12px;
                        padding-bottom: 12px;
                        letter-spacing: 2px;
                        text-transform: none;
                        color: $color-white;
                    }

                    .dropdown-menu,
                    .dropdown-submenu {
                        display: none !important;
                        visibility: hidden !important;
                        opacity: 0 !important;
                    }
                }
            }
        }
    }

    /*Menu Positions*/
    &.menu-left {
        nav {
            float: left;
            margin-left: 20px;

            > ul {
                > li {
                    &.mega-menu-item {
                        position: static;

                        > .dropdown-menu {
                            right: auto;
                            left: 0;
                        }
                    }
                }
            }
        }
    }

    &.menu-center {
        > .container {
            float: none !important;
            text-align: center;
        }

        nav {
            float: none;
            display: inline-block;
            vertical-align: top;
        }
    }

    /*Menu options*/
    &.menu-onclick {
        nav {
            > ul {
                > li {

                    &:hover:not(.hover-active) > ul,
                    .dropdown-submenu:hover > ul {
                        opacity: 0 !important;
                        visibility: hidden !important;
                        display: inherit !important;
                    }

                    &.hover-active > ul,
                    .hover-active.dropdown-submenu > ul {
                        opacity: 1 !important;
                        visibility: visible !important;
                        display: block;
                    }
                }
            }
        }
    }

    &.menu-rounded-dropdown {
        nav {
            > ul {
                > li {
                    .dropdown-menu {
                        border-radius: 6px;
                    }

                    > .dropdown-menu {
                        margin-top: -6px !important;
                    }
                }
            }
        }
    }
}


@-webkit-keyframes fade-in-out {
