        opacity: 1;
    }
}

@keyframes fade-in-out {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

/* ----------------------------------------------------------------
Dots Menu
-----------------------------------------------------------------*/

#dotsMenu {
    position: fixed;
    right: 36px;
    top: 50%;
    bottom: auto;
    transform: translateY(-50%);
    z-index: 99;

    &:before {
        content: "";
        background: rgba($dark, .7);
        position: absolute;
        width: 32px;
        right: -8px;
        height: 100%;
        border-radius: 20px;
        top: -24px;
        box-shadow: 0 4px 9px 0 rgba(43, 43, 49, 0.1), 0 7px 14px 0 rgba(43, 43, 49, 0.13);
    }

    ul {
        list-style: none;
        padding-bottom: 6px;

        li {
            text-align: right;
            padding: 0px;
            width: 15px;
            height: 15px;
            margin-bottom: 12px;
            background-color: $light;
            border-radius: 50%;
            transform: scale(0.6);
            transition: transform 0.3s, background-color 0.5s;

            &:hover {
                background-color: $color-theme;
                transform: scale(1);
            }

            &.current {
                background-color: $color-theme;
            }

            a {
                position: absolute;
                width: 40px;
                height: 40px;
                line-height: 1.5;
                border-radius: 50%;
                left: -14px;
                top: -14px;

                span {
                    position: absolute;
                    width: auto;
                    top: 8px;
                    right: 25px;
                    white-space: nowrap;
                    margin-right: 14px;
                    font-size: 12px;
                    font-weight: 800;
                    color: $color-white;
                    transition: transform 0.2s, opacity 0.2s;
                    opacity: 0;
                    transform-origin: 100% 50%;
                    background-color: rgba($color-black, 0.6);
                    border-radius: 100px;
                    line-height: 26px;
                    padding: 0 12px;
                    text-transform: uppercase;
                }

                &:hover {
                    &:before {
                        transform: scale(1);
                    }
                }

                &:hover,
                &.active {
                    &:before {
                        background-color: $color-theme;
                    }

                    > span {
                        opacity: 1;
                        transform: scale(1);
                    }
                }
            }
        }
    }
}

.mainMenu-open #dotsMenu {
    z-index: -1 !important;
}


/* ----------------------------------------------------------------
Overlay Menu
-----------------------------------------------------------------*/

.menu-overlay-active {

    #topbar.topbar-transparent.topbar-fullwidth,
    #header.header-fullwidth.header-transparent #header-wrap,
    #header.header-fullwidth.header-transparent #header-wrap .header-extras {
        border: 0;
    }

    #header {
        &.header-sticky.dark #header-wrap {
            background-color: transparent !important;
        }

        #mainMenu {
            z-index: 200;

            nav {
                opacity: 1 !important;
                display: inherit !important;
                pointer-events: auto;

                > ul {
                    transform: translateY(-50%);
                    top: 50%;

                    > li {
                        > a {
                            opacity: 1;
                        }
                    }
                }
            }
        }

        .header-extras a:not(#menu-overlay-trigger) {
            opacity: 0;
            visibility: hidden;
        }

        #menu-overlay-trigger {
            .lines {

                &,
                &:before,
                &:after {
                    background-color: $color-white;
                }
            }
        }
    }
}


/*Menu split*/

.header-logo-center,
.menu-split {
    #logo {
        position: absolute !important;
        width: 100%;
        text-align: center;
        margin: 0;
        float: none;
        height: 100px;

        > a {
            display: inline-block;
        }
    }

    #mainMenu {
        nav {
            float: none;
            margin: 0;
            position: relative;
            z-index: 1;

            > ul {
                z-index: 198;

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}


/*Menu Dark*/

.dark #mainMenu:not(.light):not(.menu-rounded),
#mainMenu.dark:not(.light):not(.menu-rounded) {
    nav {
        > ul {
            > li {
                &:before {
                    color: $dark-light;
                }

                .dropdown-menu {
                    background-color: $dark;
                    border: 1px solid $darker;

                    > li {

                        > a,
                        > span,
                        [class*="col-"] > ul > li > a {
                            color: $dark-light;

                            &:hover {
                                color: $color-theme;
                            }
                        }
                    }
                }

                .dropdown-submenu {
                    &:before {
                        color: $dark-light;
                    }
                }

                &.mega-menu-item {
                    .mega-menu-content {
                        .mega-menu-title {
                            color: $light;
                        }
                    }
                }

                &.current,
                &:hover,
                &:focus,
                &.hover-active {

                    > a,
                    > span {
                        color: $color-theme;
                    }
                }
            }
        }
    }
}

#menu-overlay-trigger {
    z-index: 299 !important;
}


/*Menu on header fullwidth */

#header.header-fullwidth {
    #mainMenu:not(.menu-left):not(.menu-center) {
        nav {
            > ul {
                > li {
                    &.mega-menu-item {
                        > .dropdown-menu {
                            left: auto;
                            right: 0;
                        }
                    }
                }
            }
        }
    }

    #mainMenu.menu-center {
        nav {
            > ul {
                > li {
                    &.mega-menu-item {
                        > .dropdown-menu {
                            right: auto;
                            left: 50%;
                            transform: translateX(-50%);
                        }
                    }
                }
            }
        }
    }
}


/* ----------------------------------------------------------------
Page Menu
-----------------------------------------------------------------*/

.page-menu {
    > .container {
        position: relative;
    }

    position: relative;
    height: 46px;
    line-height: 46px;
    background-color: #f3f3f3;

    .menu-title {
        float: left;
        color: $color-body;
        font-size: 18px;

        span {
            font-weight: 600;
        }
    }

    nav {
        float: right;

        ul {
            list-style: none;
            padding: 0;
            margin: 0;
            position: relative;
        }

        li {
            position: relative;
            margin-right: 2px;
            border: 0;
            display: inline-block;
            float: none;

            a {
                display: block;
                position: relative;
                padding: 8px 10px;
                font-size: 13px;
                line-height: 12px;
                color: $color-body;
                border-radius: 50px;
                transition: all .3s ease;
                border-width: 2px;
                border-style: solid;
                border-color: transparent;
            }

            &:hover,
            &.active {
                a {
                    color: $color-white;
                    background-color: $color-theme;
                }
            }

            &:last-child {
                margin-right: 0;
            }
        }
    }

    &.menu-outline {
        nav {
            li {

                &:hover,
                &.active {
                    a {
                        border-color: $color-theme;
                        color: $color-theme;
                        background-color: transparent;
                    }
                }
            }
        }
    }

    &.menu-lines {
        nav {
            li {
                a {
                    border-radius: 0;
                    border: none;
                    background-color: transparent;

                    &:after {
                        content: '';
                        position: absolute;
                        top: auto;
                        bottom: -8px;
                        left: 50%;
                        width: 0;
                        height: 2px;
                        transition: all 0.3s ease;
                    }
                }

                &:hover,
                &.active {
                    a {
                        color: $color-theme;

                        &:after {
                            width: 100%;
                            left: 0%;
                            background-color: $color-theme;
                        }
                    }
                }
            }
        }
    }

    &.menu-line-bottom {
        border-bottom: 2px solid #ebebeb;
        background-color: $color-white !important;
    }

    &.menu-classic {
        nav {
            li {
                a {
                    color: $color-body;
                    border-radius: 4px;
                }

                &:hover,
                &.active {
                    a {
                        background-color: $color-theme;
                        color: $color-white;
                    }
                }
            }
        }
    }

    &.menu-light {
        nav {
            li {
                a {
                    color: $color-body;
                    background-color: transparent;
                }

                &:hover,
                &.active {
                    a {
                        border-color: #ebebeb;
                    }
                }
            }
        }
    }

    &.menu-dark {
        nav {
            li {
                a {
                    color: $dark;
                    background-color: transparent;
                }

                &:hover,
                &.active {
                    a {
                        border-color: $dark;
                    }
                }
            }
        }
    }

    &.menu-default {
        nav {
            li {
                a {
                    color: none;
                    border-radius: 0;
                    background-color: transparent;
                }

                &:hover,
                &.active {
                    a {
                        background-color: transparent;
                        color: $color-theme;
                    }
                }
            }
        }
    }

    &.menu-creative {
        nav {
            li {
                a {
                    border-radius: 0;
                    border: none;
                    background-color: transparent;

                    &:after {
                        background: $dark;
                        content: "";
                        height: 2px;
                        left: 0;
                        opacity: 0;
                        position: absolute;
                        top: 100%;
                        transform: translateY(10px);
                        transition: opacity 0.3s ease 0s, transform 0.3s ease 0s;
                        width: 100%;
                    }
                }

                &:hover,
                &.active {
                    a {
                        color: $color-body;

                        &:after {
                            opacity: 1;
                            transform: translateY(0px);
                        }
                    }
                }
            }
        }
    }

    &.menu-colored-background {
        background-color: $color-theme;

        .menu-title {
            color: $color-white;
        }

        nav {
            li {
                a {
                    color: $color-white;
                    border-width: 0;
                }

                &:hover,
                &.active {
                    a {
                        color: $color-theme;
                        background-color: $color-white;
                    }
                }
            }
        }
    }
}

#menu-responsive-icon {
    background-color: #fff;
    border-radius: 6px;
    cursor: pointer;
    font-size: 14px;
    height: 36px;
