/* ----------------------------------------------------------------
Side Push Panel
-----------------------------------------------------------------*/

#side-panel {
    background-color: $light;
    height: 100%;
    width: $side-panel-width;
    position: fixed;
    left: -$side-panel-width;
    z-index: 399;
    height: 100%;
    transition: left 0.4s, right 0.4s;
    overflow: hidden;

    #close-panel {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 100;

        > i {
            cursor: pointer;
            display: block;
            width: 40px;
            height: 40px;
            font-size: 18px;
            line-height: 40px;
            color: $dark;
            text-align: center;
            background-color: rgba(0, 0, 0, 0.1);
            border-radius: 0 0 0 2px;
        }
    }

    .side-panel-wrap {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        right: -18px;
        bottom: 0;
        height: 100%;
        overflow-y: scroll;
        overflow-x: hidden;
        padding: 30px;

        .logo {
            margin-bottom: 20px;
        }
    }

    /*dark version*/
    &.dark {
        background-color: $dark;

        /*Main Menu*/
        #mainMenu {
            nav {
                > ul {
                    > li {
                        &:before {
                            color: $dark-light;
                        }

                        > a {
                            color: $light;

                            .menu-sub-title {
                                color: $light;
                            }
                        }
                    }
                }
            }
        }

        #close-panel {
            > i {
                color: $light;
                background-color: rgba(255, 255, 255, 0.1);
            }
        }
    }
}

.side-panel {
    #wrapper {
        height: 100%;
        position: relative;
        transition: left 0.4s, right 0.4s;
        left: 0;

        &:after {
            position: absolute;
            top: 0;
            right: 0;
            width: 0;
            height: 0;
            background: rgba(0, 0, 0, 0.4);
            content: '';
            opacity: 0;
            transition: opacity 0.3s;
            z-index: 9999;
        }
    }

    /* Static */
    &.side-panel-static {
        #side-panel {
            left: 0 !important;
            right: auto !important;
        }

        #close-panel,
        #side-panel-trigger {
            display: none;
        }

        #wrapper {

            > section > .container,
            > footer .footer-content > .container,
            > footer .copyright-content > .container,
            > div > .container {
                padding-left: 30px;
                width: auto;
                padding-right: 30px;
            }

            padding-left: $side-panel-width;
            transition: left 0.4s,
            right 0.4s;

            #header.header-sticky #header-wrap > .container {
                padding-left: $side-panel-width +30;
            }
        }

        &.side-panel-right {
            #side-panel {
                right: 0 !important;
                left: auto !important;
            }

            #wrapper {
                padding-left: 0;
                padding-right: $side-panel-width;

                #header.header-sticky #header-wrap > .container {
                    padding-right: $side-panel-width + 30;
                    padding-left: 30px;
                }
            }
        }
    }

    /* Reval */
    &.side-panel-reval {
        #side-panel {
            z-index: -1;
            left: 0;
            right: auto;
        }

        #wrapper {
            left: 0;
            right: auto;
            transition: left 0.4s, right 0.4s;
        }
    }

    &.side-panel-admin {
        transition: all 0.4s ease;

        #side-panel {
            width: $side-panel-width-collapsed;
            left: 0;
            right: auto;
            transition: all 0.4s ease;

            #mainMenu nav > ul > li > a > i {
                font-size: 22px;
            }

            .side-panel-wrap .logo {
                margin-left: -16px;
            }
        }

        #wrapper {
            padding-left: $side-panel-width-collapsed;
            left: 0;
            right: auto;
            transition: all 0.4s ease;

            #header #header-wrap {
                left: $side-panel-width-collapsed;
                transition: all 0.4s ease;
            }
        }
    }

    &.side-panel-active {

        /* Effect 1: Slide in on top */
        &.side-panel-slide-in {
            #side-panel {
                left: 0;
                right: auto;
            }

            #wrapper {
                cursor: url(../images/close.png), auto;

                &:after {
                    opacity: 1;
                    width: 100%;
                    height: 100%;
                    z-index: 299;
                }
            }
        }

        /* Effect 2: Reval */
        &.side-panel-reval {
            #wrapper {
                left: $side-panel-width;
                right: auto;
                box-shadow: -7px -2px 12px 0 rgba(0, 0, 0, 0.1);

                #header.header-sticky #header-wrap {
                    left: $side-panel-width;
                    right: -$side-panel-width;
                }
            }
        }

        &.side-panel-admin {
            #side-panel {
                width: $side-panel-width;
                left: 0;
                right: auto;
                /*#mainMenu nav > ul li { 
                           opacity: 0;
                       }
                       #mainMenu nav > ul > li > a > i {
                           opacity: 1 
                       }*/
            }


            #wrapper {
                padding-left: $side-panel-width;
                left: 0;
                right: auto;

                #header #header-wrap {
                    left: $side-panel-width;
                }

                #header.header-sticky #header-wrap {
                    left: $side-panel-width;
                }
            }
        }

        &.side-panel-push {
            #side-panel {
                left: 0;
                right: auto;
            }

            #wrapper {
                left: $side-panel-width;
                cursor: url(../images/close.png), auto;

                &:after {
                    opacity: 1;
                    width: 100%;
                    height: 100%;
                }

                #header.header-sticky #header-wrap > .container {
                    left: $side-panel-width;
                    right: 0;
                }
            }
        }
    }

    &.side-panel-right {
        #side-panel {
            right: -$side-panel-width;
            left: auto;

            #close-panel {
                left: 0;
                right: auto;
            }
        }

        #wrapper {
            left: auto;
            right: 0;

            #header.header-sticky #header-wrap > .container {
                transition: left 0.4s, right 0.4s;
            }
        }

        &.side-panel-reval {
            #side-panel {
                left: auto;
                right: 0;
            }

            #wrapper {
                left: auto;
                right: 0;
                transition: left 0.4s, right 0.4s;
                box-shadow: 12px -2px 12px 0 rgba(0, 0, 0, 0.1);
            }
        }

        &.side-panel-active {
            &.side-panel-slide-in {
                #side-panel {
                    right: 0;
                    left: auto;
                }
            }

            &.side-panel-reval {
                #wrapper {
                    right: $side-panel-width;
                    left: auto;

                    #header.header-sticky #header-wrap {
                        right: $side-panel-width;
                        left: -$side-panel-width;
                    }
                }
            }

            &.side-panel-admin {
                #wrapper {
                    right: $side-panel-width;
                    left: auto;

                    #header.header-sticky #header-wrap {
                        right: $side-panel-width;
                        left: -$side-panel-width;
                    }
                }
            }

            &.side-panel-push {
                #side-panel {
                    right: 0;
                    left: auto;
                }

                #wrapper {
                    right: $side-panel-width;
                    left: auto;

                    #header.header-sticky #header-wrap > .container {
                        transition: left 0.4s, right 0.4s;
                        right: $side-panel-width;
                        left: auto;
                    }
                }
            }
        }
    }
}

