/* ----------------------------------------------------------------
Main menu - Vertical
-----------------------------------------------------------------*/

#mainMenu.menu-vertical,
#mainMenu.menu-center.menu-vertical {
    max-height: 3000px;

    > .container {
        text-align: left !important;
        width: 100% !important;
    }

    clear: both;
    display: block;
    width: 100%;
    opacity: 1;
    overflow: hidden;
    transition: max-height 0.8s cubic-bezier(0.79, 0.14, 0.15, 0.86),
    opacity 0.8s cubic-bezier(0.79, 0.14, 0.15, 0.86);

    nav {
        line-height: 40px;
        float: none;
        width: 100%;
        padding-bottom: 20px;

        > ul {
            float: none;
            width: 100%;

            > li {
                padding: 0;
                margin: 0;
                clear: both;
                float: none;
                display: block;
                border: 0;

                &.dropdown {
                    &:before {
                        font-size: 12px;
                        transition: all .2s ease;
                        content: "\e95a";
                        font-family: "inspiro-icons";
                        opacity: 0.6;
                        position: absolute;
                        right: 2px;
                        color: $dark-grey;
                    }
                }

                &.hover-active {
                    &:before {
                        transform: rotate(90deg);
                    }
                }

                > a {
                    display: block;
                    padding: 12px 0;
                    font-size: 13px !important;
                    border: 0;
                    border-radius: 0;

                    &:after {
                        display: none;
                    }

                    .menu-sub-title {
                        display: none !important;
                    }
                }


                .dropdown-submenu {
                    &:not(.hover-active):hover {
                        > ul {
                            animation: none !important;
                        }
                    }
                }

                &:not(.hover-active):hover > ul {
                    animation: none !important;
                }

                .dropdown-menu {
                    background-image: none !important;
                    max-height: 0;
                    overflow: hidden;
                    opacity: 0;
                    transition: max-height 0.5s cubic-bezier(0.77, 0, 0.175, 1), opacity 0.5s cubic-bezier(0.77, 0, 0.175, 1);
                    position: static;
                    clear: both;
                    float: none;
                    box-shadow: none;
                    border: 0 !important;
                    min-width: 100%;
                    margin: 0;
                    border-radius: 0px;
                    box-shadow: none;
                    padding: 0 16px;

                    > li {

                        > a,
                        > span,
                        [class*="col-"] > ul > li > a {
                            padding: 12px 0;
                            display: block;
                        }
                    }


                }

                &.mega-menu-item {
                    position: relative;

                    .mega-menu-content {
                        padding: 0 0 0 15px;

                        .mega-menu-title {
                            padding-top: 12px;
                            padding-bottom: 12px;
                        }

                        > a,
                        > span,
                        [class*="col-"] > ul > li > a {
                            padding: 10px 0px 10px 0px;
                        }

                        [class*="col-"] > ul > li:first-child > a {
                            padding-top: 12px;
                        }

                        [class*="col-"] > ul > li:last-child > a {
                            padding-bottom: 12px;
                        }
                    }
                }

                .dropdown-submenu {
                    position: relative;

                    &:before {
                        right: 0;
                        top: 0;
                        font-size: 12px;
                    }

                    &.hover-active {
                        &:before {
                            transform: rotate(90deg);
                        }
                    }
                }
            }

            li {
                > .dropdown-menu {
                    border: 0 none;
                    background-color: transparent;
                    display: block;

                    > .dropdown-submenu {
                        position: relative;

                        &:before {
                            right: 0;
                        }
                    }


                }

                &.hover-active {
                    > .dropdown-menu {
                        visibility: visible;
                        opacity: 1;
                        max-height: 2000px;
                    }
                }
            }

            [class*="col-lg"] {
                max-width: 100%;
                padding: 0;
                margin: 0;
                width: 100%;
                flex: 100%;
            }
        }
    }

    &.menu-sub-title {
        nav {
            > ul {
                > li {
                    line-height: unset !important;

                    .dropdown-submenu {
                        &:before {
                            top: 0;
                        }
                    }
                }
            }
        }
    }

    &.menu-slide {
        nav {
            > ul {
                > li {
                    visibility: visible;
                    opacity: 1;
                }
            }
        }
    }

    &.menu-rounded {
        nav {
            > ul {
                > li {

                    &.current,
                    &:hover,
                    &:focus {
                        > a {
                            background-color: unset !important;
                        }
                    }
                }
            }
        }
    }

    &.menu-hover-background {
        nav {
            > ul {
                > li {

                    &.current,
                    &:hover,
                    &:focus {
                        background-color: unset;

                        > a {
                            color: unset !important;
                        }
                    }
                }
            }
        }
    }
}

